<template>
    <div class="formio-wrapper">
        <div id="formio"></div>
        <div class="button-container lb">
            <button class="qg-btn btn-default m-md-2" @click="saveAndExitButtonHandler" v-if="currentPageIndex !== 0 && isSubmitted === false">Save and
                exit
            </button>

            <button class="qg-btn btn-primary m-md-2 offset-1" @click="saveAndContinueButtonHandler" v-if="isLastPanel === false && isSubmitted === false">
                Next
            </button>

            <!-- form having no payment -->
            <button class="qg-btn btn-primary m-md-2 offset-1"
                    @click="submitFormHandler"
                    v-if="isLastPanel === true && isSubmitted === false && hasPayment === 'false'"
            >
                Submit
            </button>

            <a class="qg-btn btn-link" @click="prevPageHandler" v-if="currentPageIndex !== 0 && isSubmitted === false">Back</a>

            <!-- form in review submission -->
            <button class="qg-btn btn-default m-md-2 offset-1" @click="prevPageHandler" v-if="currentPageIndex !== 0  && isSubmitted === true">Previous</button>
            <button class="qg-btn btn-default m-md-2 offset-1" @click="nextPageHandler" v-if="isLastPanel === false && isSubmitted === true">Next</button>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import {mapActions, mapGetters} from 'vuex'
import {Formio} from "formiojs";
import Panel from "../models/panel";
import Helper from "../models/helper";
import axios from 'axios'

export default {
    name: 'FormWrapper',
    props: {
        savedApplicationPage: {
            type: String,
            required: true
        },
        thankYouPage: {
            type: String,
            required: true
        },
        projectUrl: {
            type: String,
            required: true
        },
        wizardUrl: {
            type: String,
            required: true
        },
        createFormUrl: {
            type: String,
            required: true
        },
        userId: {
            type: String,
            required: true
        },
        submissionId: {
            type: String,
            required: true
        },
        formIdentifier: {
            type: String,
            required: true
        },
        hasPayment: {
            type: String,
            required: true
        },
        paymentUrl: {
            type: String
        },
        recaptchaSiteKey: {
            type: String
        }
    },
    data() {
        return {
            formio: {},
            currentPageIndex: 0,
            isLastPanel: false,
            isSubmitted: false,
            disableSubmit: true,
            // Track which button clicked.
            buttonClicked: ''
        }
    },
    mounted() {
        Formio.setProjectUrl(this.projectUrl)
        Formio.setUser({
            _id: this.userId
        })

        Formio.createForm(document.getElementById('formio'), this.createFormUrl, {
            buttonSettings: {
                showCancel: false,
                showNext: false,
                showPrevious: false,
                showSubmit: false
            },
        }).then(wizard => {
            if (this.submissionId !== '') {
                wizard.loadSubmission().then((submission) => {
                    this.syncForm(wizard, submission)
                    this.currentPageIndex = parseInt(submission.data.currentPageIndex)
                    this.formio.setPage(this.currentPageIndex)
                    this.updateCurrentPageIndex(this.currentPageIndex)
                });
            } else {
                this.syncForm(wizard)
                this.updateCurrentPageIndex(0)
            }

            wizard.on('saveAndContinue', () => {
                this.saveAndContinueButtonHandler()
            })

            wizard.on('saveAndExit', () => {
                this.buttonClicked = 'saveAndExit'
                this.saveAndExitButtonHandler()
            })

            // When wizard show/hide a panel.
            wizard.on('buildPanelList', () => {
                this.formio.establishPages()
                this.syncForm(wizard)
            })

            // Change field on a panel.
            wizard.on('pagesChanged', () => {
                // If not valid, left list disable future steps.
                if (!wizard.isValid()) {
                    this.updateCurrentPageIndex(this.currentPageIndex)
                    this.syncForm(wizard)
                }
            })

            // Edit (go to) page on Preview Page.
            wizard.on('editPage', (pageName) => {
                this.currentPageIndex = this.getPageIndexByPageName(pageName)
                this.updateCurrentPageIndex(this.currentPageIndex)
            })

            // Post "draft" saved action
            wizard.on('saveDraft', () => {
                this.afterSaveDraftHandler()
            })

            // Post "submit" saved action
            wizard.on('submitDone', () => {
                window.location.href = this.thankYouPage
            })

            if (
                window.formioWizard?.customController &&
                window[window.formioWizard.customController] &&
                typeof window[window.formioWizard.customController] === "function"
            ) {
                window[window.formioWizard.customController](wizard, this);
            }
        });
    },
    computed: {
        ...mapGetters([
            'getCurrentPageIndex',
            'getIsLastPanel'
        ])
    },
    watch: {
        getCurrentPageIndex() {
            this.gotoPageHandler()
        }
    },
    methods: {
        /**
         * Get page index by given page name.
         */
        getPageIndexByPageName(pageName) {
            let pageIndex = 0;

            this.formio.pages.filter(function (e, i) {
                if (pageName === e.key) {
                    pageIndex = i;
                }
            })
            return pageIndex;
        },

        /**
         * Build formio
         * @param wizard
         * @param submission
         */
        syncForm(wizard, submission = {}) {
            if (typeof submission.state !== 'undefined' && submission.state === 'submitted') {
                wizard.options.readOnly = true;
                wizard.options.renderMode = 'html';
                wizard.options.editMode = false;
                wizard.redraw();
            }

            this.formio = wizard
            this.currentPageIndex = wizard.page
            this.isLastPanel = wizard.pages.length === wizard.page + 1
            this.isSubmitted = typeof submission.state !== 'undefined' && submission.state === 'submitted'

            let panelList = this.buildPanelList(wizard.pages ?? [], wizard.page)
            let formHelperConfig = wizard.form.config.helpers ?? []
            let helpers = this.buildHelpers(formHelperConfig)

            this.setCurrentPageIndex(wizard.page)
            this.setPanelList(panelList)
            this.setHelpers(helpers)
        },

        /**
         * Build panel list data.
         */
        buildPanelList(wizardPages, currentPage) {
            if (wizardPages.length === 0) {
                return []
            }

            return wizardPages.map((panel, index) => {
                let isActive = false
                let isDisabled = true

                if (index === currentPage) {
                    isActive = true
                }

                if (index <= currentPage) {
                    isDisabled = false
                }

                return new Panel({
                    isActive,
                    isDisabled,
                    title: panel.component.title,
                    key: panel.key,
                    index
                });
            })
        },

        /**
         * Build helper data.
         */
        buildHelpers(helpers) {
            if (helpers.length === 0) {
                return []
            }

            return helpers.map((helper) => {
                return new Helper({
                    pageKey: helper.pageKey,
                    header: helper.header,
                    content: helper.content,
                })
            })
        },

        /**
         * After draft has been saved handler. Listen to "FormIO.js" emit "saveDraft" event.
         */
        afterSaveDraftHandler() {
            switch (true) {
                // Having payment - utilize the `_id` and submit to payment server.
                case this.hasPayment && this.buttonClicked === 'payNow':
                    this.payNowHandler();
                    break;
                // No payment - redirect to saved application page.
                default:
                    window.location.href = this.savedApplicationPage
            }
        },

        /**
         * "payNow" handler. Redirect user to BPoint payment page.
         */
        payNowHandler() {
            // When form is not saved or payment is not success. Return to this URL at the end.
            let draftSubmissionUrl =
                `${document.location.protocol}//${document.location.host}${document.location.pathname}?submissionId=${this.formio.submission._id}`;

            axios
                .post(this.paymentUrl, {
                    data: {
                        'merchantShortName': this.formio.data.merchantShortName,
                        'billerCode': this.formio.data.billerCode,
                        'uuid': this.formio.data.uuid,
                        'email': this.formio.data.email,
                        'addressListDataGrid': this.formio.data.addressListDataGrid,
                        'totalPrice': this.formio.data.totalPrice,
                        'paySuccess': this.formio.data.paySuccess,
                        'state': this.formio.data.state,
                    },
                    draftSubmissionUrl: draftSubmissionUrl,
                    thankYouPageUrl: this.thankYouPage,
                    submissionUrl: this.wizardUrl + '/submission/' + this.formio.submission._id,
                    submissionId: this.formio.submission._id,
                    formIdentifier: this.formIdentifier
                })
                .then(function (response) {
                    window.location.href = response.data.payment_url
                })
                .catch(function (error) {
                    console.log(error);
                });
        },

        /**
         * Go to previous page.
         */
        prevPageHandler() {
            this.currentPageIndex = this.getCurrentPageIndex - 1
            this.formio.setPage(this.getCurrentPageIndex)
            this.updateCurrentPageIndex(this.currentPageIndex)
        },

        /**
         * Go to next page.
         */
        nextPageHandler() {
            this.currentPageIndex = this.getCurrentPageIndex + 1
            this.formio.setPage(this.getCurrentPageIndex)
            this.updateCurrentPageIndex(this.currentPageIndex)
        },

        /**
         * Update FormWrapper's local data.
         */
        gotoPageHandler() {
            this.currentPageIndex = this.getCurrentPageIndex
            this.formio.setPage(this.getCurrentPageIndex)
            this.isLastPanel = this.getIsLastPanel
            this.scrollToTop()
        },

        /**
         * Save and Continue
         */
        saveAndContinueButtonHandler() {
            this.formio
                .nextPage()
                .then(() => {
                    this.updateCurrentPageIndex(parseInt(this.currentPageIndex) + 1)
                    this.formio.getComponent('currentPageIndex').setValue(this.getCurrentPageIndex.toString())
                })
                .finally(() => {
                    this.scrollToTop()
                })
        },

        /**
         * Save and Exit
         */
        saveAndExitButtonHandler() {
            this.saveDraftHandler()
            this.scrollToTop()
        },

        /**
         * Submit the form
         */
        submitFormHandler() {
            this.formio.getComponent('state').setValue('submitted')

            this.formio
                .submit()
                .finally(() => {
                    this.scrollToTop()
                });
        },

        /**
         * Save form as Draft.
         */
        saveDraftHandler() {
            this.formio.draftEnabled = true
            this.formio.savingDraft = false
            this.formio.getComponent('currentPageIndex').setValue(this.getCurrentPageIndex.toString())
            this.formio.saveDraft();
        },

        /**
         * Scroll to the top
         */
        scrollToTop() {
            window.scroll({
                top: 0,
                behavior: 'smooth',
            });
        },

        ...mapActions([
            'setCurrentPageIndex',
            'setPanelList',
            'setHelpers',
            'setCurrentHelper',
            'updateCurrentPageIndex',
        ])
    },
}
</script>

<style scoped>
</style>
