import {createStore} from 'vuex'

export const store = createStore({
    state() {
        return {
            currentPageIndex: 0,
            panelList: [],
            isLastPanel: false,
            helpers: [],
            currentHelper: {},
            submissionId: '',
            userId: '',
            submissionStatus: 'draft'
        }
    },
    getters: {
        getCurrentPageIndex(state) {
            return state.currentPageIndex;
        },
        getPanelList(state) {
            return state.panelList;
        },
        getIsLastPanel(state) {
            return state.isLastPanel;
        },
        getHelpers(state) {
            return state.helpers;
        },
        getCurrentHelper(state) {
            return state.currentHelper;
        },
        getSubmissionId(state) {
            return state.submissionId;
        },
        getUserId(state) {
            return state.userId;
        },
        getSubmissionStatus(state) {
            return state.submissionStatus;
        },
    },
    mutations: {
        setCurrentPageIndex(state, currentPageIndex) {
            state.currentPageIndex = currentPageIndex;
        },
        setPanelList(state, panelList) {
            state.panelList = panelList;
        },
        setIsLastPanel(state, isLastPanel) {
            state.isLastPanel = isLastPanel;
        },
        setHelpers(state, helpers) {
            state.helpers = helpers;
        },
        setCurrentHelper(state, currentHelper) {
            state.currentHelper = currentHelper;
        },
        setSubmissionId(state, submissionId) {
            state.submissionId = submissionId;
        },
        setUserId(state, userId) {
            state.userId = userId;
        },
        setSubmissionStatus(state, submissionStatus) {
            state.submissionStatus = submissionStatus;
        },
    },
    actions: {
        setCurrentPageIndex({commit}, currentPageIndex) {
            // set current page index
            commit('setCurrentPageIndex', currentPageIndex)

        },
        setPanelList({commit}, panelList){
            commit('setPanelList', panelList)
        },
        setIsLastPanel({commit}, isLastPanel){
            commit('setIsLastPanel', isLastPanel)
        },
        setHelpers({commit}, helpers){
            commit('setHelpers', helpers)
        },
        setCurrentHelper({commit}, currentHelper){
            commit('setCurrentHelper', currentHelper)
        },
        setSubmissionId({commit}, submissionId){
            commit('setSubmissionId', submissionId)
        },
        setUserId({commit}, userId){
            commit('setUserId', userId)
        },
        setSubmissionStatus({commit}, submissionStatus){
            commit('setSubmissionStatus', submissionStatus)
        },
        /**
         * Update current page by given page index id.
         * @param context Do NOT destruct due to IDE.
         * @param currentPageIndex
         */
        updateCurrentPageIndex(context, currentPageIndex) {
            context.commit('setCurrentPageIndex', currentPageIndex)

            // set left nav
            let panelList = context.state.panelList.map((panel, index) => {
                panel.isActive = false;
                panel.disabled = true;

                if (index < context.state.currentPageIndex) {
                    panel.isDisabled = false
                }

                if (index === context.state.currentPageIndex) {
                    panel.isActive = true
                    panel.isDisabled = false
                }

                return panel;
            })
            context.commit('setPanelList', panelList)
            context.commit('setIsLastPanel', context.state.panelList.length === currentPageIndex + 1)

            // set right helpers
            context.state.helpers.forEach((helper) => {
                if(helper.pageKey === context.state.panelList[context.state.currentPageIndex].key) {
                    context.commit('setCurrentHelper', helper)
                }
            })
        }
    }
})
