<template>
    <div id="formio-land-form" class="container container-fluid qg-site-width">
        <div id="qg-three-col" class="row no-secondary-nav">
            <div id="qg-primary-content" role="main">
                <h1>{{ formName }}<br/><em>(Land Act 1994)</em></h1>
                <FormWrapper
                    :projectUrl="projectUrl"
                    :wizardUrl="wizardUrl"
                    :createFormUrl="createFormUrl"
                    :formIdentifier="formIdentifier"
                    :savedApplicationPage="savedApplicationPage"
                    :thankYouPage="thankYouPage"
                    :userId="userId"
                    :submissionId="submissionId"
                    :hasPayment="hasPayment"
                    :paymentUrl="paymentUrl"
                    :recaptchaSiteKey="recaptchaSiteKey"
                />
                <div class="clearfix"></div>
                <div class="qg-content-footer">
                    <p>This site is protected by reCAPTCHA and the Google
                        <a href="https://policies.google.com/privacy">Privacy Policy</a> and
                        <a href="https://policies.google.com/terms">Terms of Service</a> apply.
                    </p>
                    <dl>
                        <dt>Version:</dt>
                        <dd>2022/01</dd>
                    </dl>
                </div>
                <div id="qg-options" class="row justify-content-sm-end"></div>
            </div>
            <aside id="qg-secondary-content">
                <div class="qg-aside qg-contact">
                    <RightSideBar/>
                </div>
            </aside>
            <LeftSideBar :formName="formName"/>
        </div>
    </div>
</template>

<script>
import FormWrapper from './components/FormWrapper'
import RightSideBar from './components/RightSideBar'
import LeftSideBar from './components/LeftSideBar'

export default {
    name: 'App',
    components: {
        LeftSideBar,
        FormWrapper,
        RightSideBar
    },
    data() {
        return {
            // FormIO
            projectUrl: window.formioWizard.appProjectUrl,
            wizardUrl: window.formioWizard.appWizardUrl,
            createFormUrl: window.formioWizard.appWizardUrl,
            formIdentifier: `${window.formioWizard.appName}-${window.formioWizard.appEnv}`,
            formName: window.formioWizard.appFormName,

            // Matrix page
            savedApplicationPage: window.formioWizard.appSavedApplicationPage,
            thankYouPage: window.formioWizard.appThankYouPage,

            // submission
            userId: window.formioWizard.appUserId,
            submissionId: '',

            // payment
            hasPayment: window.formioWizard.appHasPayment,
            paymentUrl: window.formioWizard.appPaymentUrl,
            recaptchaSiteKey: window.formioWizard.appRecaptchaSiteKey,
        }
    },
    beforeMount() {
        let queryString = window.location.search;
        let urlParams = new URLSearchParams(queryString);
        let submissionId = urlParams.get('submissionId')

        this.createFormUrl = this.wizardUrl
        if (submissionId) {
            this.createFormUrl += '/submission/' + submissionId
            this.submissionId = submissionId
        }
    }
}
</script>

<style>
#formio-land-form h1 em {
    font-size: 80%;
}

.formio-component-radio:not(.date-mark-radio-button) .form-radio {
    display: flex;
    flex-direction: row;
}

/** Big radio button */
.formio-component-radio:not(.date-mark-radio-button).bulky-radio-button .form-radio {
    flex-direction: column;
}

/** Vertical stack radio button */
@media (max-width: 768px) {
    .formio-component-radio:not(.date-mark-radio-button).flex-column .form-radio {
        flex-direction: column;
    }
}

.checkbox, .radio {
    position: relative;
    display: block;
    margin-top: 10px;
    margin-bottom: 10px;
}

.formio-component-radio:not(.date-mark-radio-button) .form-radio .form-check {
    flex-grow: 2;
    padding: 32px 10px;
    margin: 5px;
    border-radius: 5px;
    border: 1px solid #979797;
    background-color: #fff;
}

.formio-component-radio:not(.date-mark-radio-button) .form-radio .form-check .form-check-label {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 0 10px;
    position: absolute;
    top: 0;
    left: 0;
}

.formio-component-radio:not(.date-mark-radio-button) .form-radio .form-check .form-check-label .form-check-input {
    position: relative;
    margin: 0;
}

input[type=checkbox], input[type=radio] {
    box-sizing: border-box;
    padding: 0;
}

.formio-component-radio:not(.date-mark-radio-button) .form-radio .form-check .form-check-label span {
    margin: 0 20px;
}

.formio-component-radio:not(.date-mark-radio-button) .form-radio .form-check.radio-selected {
    background-color: #007eb1;
    color: #fff;
}

.word-break {
    word-break: break-word;
}

.formio-component-radio:not(.date-mark-radio-button) .form-radio .form-check .form-check-label input[type=radio]:checked:after {
    width: 21px;
    height: 21px;
    background-color: #4a4a4a;
    border: 4px solid #fff;
}

.formio-component-radio:not(.date-mark-radio-button) .form-radio .form-check .form-check-label input[type=radio]:after {
    width: 24px;
    height: 24px;
    border-radius: 24px;
    top: -3px;
    position: relative;
    background-color: #fff;
    content: "";
    display: inline-block;
    visibility: visible;
    border: 1px solid #979797;
}

/* fix for select box and input box same line-height */
#formio-land-form .form-control {
    height: calc(1.5em + 0.75rem + 2px);
}

/** enforce data grid select box width */
#formio-land-form .titleSelectBox {
    width: 10rem;
}

/* vertically stacked radio buttons layout */
.formio-component-radio-vertical-stacked.formio-component-radio:not(.date-mark-radio-button) .form-radio {
    flex-direction: column;
}

/* override hidden input field style in edit grid component */
input.formio-select-autocomplete-input {
    display: none;
}
</style>
